<template>
  <div class="chat-container" :class="{ 'dark-mode': darkMode }">
    <!-- Ê†áÈ¢òÊ†è -->
    <div class="chat-header">
      <div class="header-left">
        <div class="logo">
          <svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
            <path d="M22 12h-4l-3 9L9 3l-3 9H2"></path>
          </svg>
        </div>
      </div>
      <div class="header-center">
        <h1>Êô∫ËÉΩÂåªÁñóÈóÆÁ≠îÁ≥ªÁªü</h1>
        <div class="header-subtitle">Âü∫‰∫éÂåªÁñóÁü•ËØÜÂõæË∞±ÁöÑÊô∫ËÉΩËØäÁñóÂä©Êâã</div>
      </div>
      <div class="header-right">
        <button class="icon-button" @click="toggleDarkMode" :title="darkMode ? 'ÂàáÊç¢Âà∞‰∫ÆËâ≤Ê®°Âºè' : 'ÂàáÊç¢Âà∞ÊöóËâ≤Ê®°Âºè'">
          <svg v-if="darkMode" xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
            <circle cx="12" cy="12" r="5"></circle>
            <line x1="12" y1="1" x2="12" y2="3"></line>
            <line x1="12" y1="21" x2="12" y2="23"></line>
            <line x1="4.22" y1="4.22" x2="5.64" y2="5.64"></line>
            <line x1="18.36" y1="18.36" x2="19.78" y2="19.78"></line>
            <line x1="1" y1="12" x2="3" y2="12"></line>
            <line x1="21" y1="12" x2="23" y2="12"></line>
            <line x1="4.22" y1="19.78" x2="5.64" y2="18.36"></line>
            <line x1="18.36" y1="5.64" x2="19.78" y2="4.22"></line>
          </svg>
          <svg v-else xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
            <path d="M21 12.79A9 9 0 1 1 11.21 3 7 7 0 0 0 21 12.79z"></path>
          </svg>
        </button>
      </div>
    </div>
    
    <!-- Ê∂àÊÅØÂàóË°®Âå∫Âüü -->
    <div class="message-list" ref="messageList">
      <div class="welcome-message" v-if="messages.length === 0">
        <div class="welcome-icon">
          <svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
            <path d="M3 9l9-7 9 7v11a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2z"></path>
            <polyline points="9 22 9 12 15 12 15 22"></polyline>
          </svg>
        </div>
        <h2>Ê¨¢Ëøé‰ΩøÁî®ÂåªÁñóÈóÆÁ≠îÁ≥ªÁªü</h2>
        <p>ÊÇ®ÂèØ‰ª•ËØ¢ÈóÆ‰ªª‰ΩïÂåªÁñóÁõ∏ÂÖ≥ÈóÆÈ¢òÔºå‰æãÂ¶ÇÔºö</p>
        <div class="suggestion-chips">
          <div class="chip" v-for="(suggestion, index) in suggestions" :key="index" @click="useExample(suggestion)">
            {{ suggestion }}
          </div>
        </div>
      </div>
      
      <transition-group name="message-fade">
        <div 
          v-for="message in messages" 
          :key="message.id" 
          :class="['message-bubble', message.isUser ? 'user' : 'system']"
        >
          <div class="message-avatar">
            <div class="avatar-icon">
              <span v-if="message.isUser">ÊÇ®</span>
              <svg v-else xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
                <path d="M19 21v-2a4 4 0 0 0-4-4H9a4 4 0 0 0-4 4v2"></path>
                <circle cx="12" cy="7" r="4"></circle>
              </svg>
            </div>
          </div>
          <div class="message-wrapper">
            <div class="message-header">
              <span>{{ message.isUser ? 'ÊÇ®ÁöÑÈóÆÈ¢ò' : 'Á≥ªÁªüÂõûÂ§ç' }}</span>
              <span class="message-time">{{ formatTime(message.timestamp) }}</span>
            </div>
            <div class="message-content" v-html="formatMessage(message.text)"></div>
            <div class="message-actions" v-if="!message.isUser">
              <button class="action-btn" @click="copyToClipboard(message.text)" title="Â§çÂà∂ÂõûÁ≠î">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
                  <rect x="9" y="9" width="13" height="13" rx="2" ry="2"></rect>
                  <path d="M5 15H4a2 2 0 01-2-2V4a2 2 0 012-2h9a2 2 0 012 2v1"></path>
                </svg>
              </button>
            </div>
          </div>
        </div>
      </transition-group>
      
      <div class="typing-indicator" v-if="isLoading">
        <div class="dot"></div>
        <div class="dot"></div>
        <div class="dot"></div>
      </div>
    </div>

    <!-- ËæìÂÖ•Âå∫Âüü -->
    <div class="input-area">
      <div class="input-wrapper">
        <input
          v-model.trim="inputText"
          placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÂåªÁñóÈóÆÈ¢ò..."
          @keyup.enter="sendMessage"
          :disabled="isLoading"
          ref="inputField"
        />
        <button 
          :disabled="!inputText || isLoading"
          @click="sendMessage"
          class="send-button"
        >
          <span v-if="isLoading" class="loading-spinner"></span>
          <svg v-else xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
            <line x1="22" y1="2" x2="11" y2="13"></line>
            <polygon points="22 2 15 22 11 13 2 9 22 2"></polygon>
          </svg>
        </button>
      </div>
      <div class="input-footer">
        <span>Êåâ Enter ÂèëÈÄÅ | <kbd>Esc</kbd> Ê∏ÖÁ©∫ËæìÂÖ•Ê°Ü | <kbd>/</kbd> Êü•ÁúãÁ§∫‰æã</span>
      </div>
    </div>
    
    <!-- Ê∂àÊÅØÈÄöÁü• -->
    <div class="toast" v-if="toast.show" :class="toast.type">
      {{ toast.message }}
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      inputText: '',
      messages: [],
      messageId: 0,
      isLoading: false,
      darkMode: false,
      suggestions: [
        'Á≥ñÂ∞øÁóÖÁöÑÁóáÁä∂ÊúâÂì™‰∫õÔºü',
        'È´òË°ÄÂéãÂ¶Ç‰ΩïÈ¢ÑÈò≤Ôºü',
        'Â§¥ÁóõÂèØËÉΩÊòØ‰ªÄ‰πàÂéüÂõ†Ôºü',
        'ÊÑüÂÜíËçØÂíåÂèëÁÉßËçØÊúâ‰ªÄ‰πàÂå∫Âà´Ôºü',
        'Â¶Ç‰ΩïÊ≠£Á°Æ‰ΩøÁî®ÊäóÁîüÁ¥†Ôºü'
      ],
      toast: {
        show: false,
        message: '',
        type: 'info',
        timeout: null
      }
    };
  },
  
  mounted() {
    // Ê£ÄÊü•Êú¨Âú∞Â≠òÂÇ®ÁöÑÊöóÈªëÊ®°ÂºèËÆæÁΩÆ
    const savedDarkMode = localStorage.getItem('darkMode') === 'true';
    this.darkMode = savedDarkMode;
    
    // ÈîÆÁõòÂø´Êç∑ÈîÆÁõëÂê¨
    document.addEventListener('keydown', this.handleKeyDown);
    
    // Ëá™Âä®ËÅöÁÑ¶ËæìÂÖ•Ê°Ü
    this.$nextTick(() => {
      this.$refs.inputField.focus();
    });
  },
  
  beforeDestroy() {
    document.removeEventListener('keydown', this.handleKeyDown);
  },
  
  methods: {
    // Â§ÑÁêÜÈîÆÁõòÂø´Êç∑ÈîÆ
    handleKeyDown(e) {
      // ESC Ê∏ÖÁ©∫ËæìÂÖ•
      if (e.key === 'Escape') {
        this.inputText = '';
        this.$refs.inputField.focus();
      }
      
      // ÊñúÊù†ÊòæÁ§∫Â∏ÆÂä©
      if (e.key === '/' && this.inputText === '') {
        e.preventDefault();
        this.showToast('ËØ∑ÈÄâÊã©‰∏ãÊñπÁöÑÁ§∫‰æãÈóÆÈ¢ò', 'info');
      }
    },
    
    // ‰ΩøÁî®Á§∫‰æãÈóÆÈ¢ò
    useExample(text) {
      this.inputText = text;
      this.sendMessage();
    },
    
    // ÊòæÁ§∫ÈÄöÁü•Ê∂àÊÅØ
    showToast(message, type = 'info') {
      if (this.toast.timeout) {
        clearTimeout(this.toast.timeout);
      }
      
      this.toast.show = true;
      this.toast.message = message;
      this.toast.type = type;
      
      this.toast.timeout = setTimeout(() => {
        this.toast.show = false;
      }, 3000);
    },
    
    // Â§çÂà∂ÊñáÊú¨Âà∞Ââ™Ë¥¥Êùø
    copyToClipboard(text) {
      navigator.clipboard.writeText(text).then(() => {
        this.showToast('Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø', 'success');
      }).catch(err => {
        this.showToast('Â§çÂà∂Â§±Ë¥•', 'error');
        console.error('Êó†Ê≥ïÂ§çÂà∂: ', err);
      });
    },
    
    // ÂàáÊç¢ÊöóÈªëÊ®°Âºè
    toggleDarkMode() {
      this.darkMode = !this.darkMode;
      localStorage.setItem('darkMode', this.darkMode);
    },
    
    // Ê†ºÂºèÂåñÊ∂àÊÅØÂÜÖÂÆπÔºåÂ§ÑÁêÜÊç¢Ë°åÂíåÁâπÊÆäÊ†áËÆ∞
    formatMessage(text) {
      if (!text) return '';
      
      // Â§ÑÁêÜÊç¢Ë°å
      let formatted = text.replace(/\n/g, '<br>');
      
      // Â§ÑÁêÜ„Äê„Äë‰∏≠ÁöÑÂÜÖÂÆπ‰∏∫È´ò‰∫Æ
      formatted = formatted.replace(/„Äê(.+?)„Äë/g, '<span class="highlight">$1</span>');
      
      // Â§ÑÁêÜÂ±ûÊÄß‰ø°ÊÅØÂíåÂÖ≥Á≥ª‰ø°ÊÅØÁöÑÊ†áÈ¢ò
      formatted = formatted.replace(/Â±ûÊÄß‰ø°ÊÅØÔºö/g, '<strong class="info-title">Â±ûÊÄß‰ø°ÊÅØÔºö</strong>');
      formatted = formatted.replace(/Áõ∏ÂÖ≥‰ø°ÊÅØÔºö/g, '<strong class="info-title">Áõ∏ÂÖ≥‰ø°ÊÅØÔºö</strong>');
      
      // Â§ÑÁêÜÂàóË°®È°π
      formatted = formatted.replace(/- (.+)/g, '<div class="list-item">‚Ä¢ $1</div>');
      
      return formatted;
    },
    
    // Ê†ºÂºèÂåñÊó∂Èó¥
    formatTime(timestamp) {
      if (!timestamp) return '';
      const date = new Date(timestamp);
      return `${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`;
    },
    
    // ‰ºòÂåñÂêéÁöÑÊï∞ÊçÆÊèêÂèñÊñπÊ≥ï
    findAnswerInData(data) {
      if (!data?.data?.results || data.data.results.length === 0) {
        return 'ÂæàÊä±Ê≠âÔºåÊöÇÊó∂Ê≤°ÊúâÊâæÂà∞Áõ∏ÂÖ≥‰ø°ÊÅØ„ÄÇËØ∑Â∞ùËØïÊç¢‰∏ÄÁßçÊñπÂºèÊèêÈóÆÔºåÊàñËÄÖÂí®ËØ¢ÂÖ∂‰ªñÂåªÁñóÈóÆÈ¢ò„ÄÇ';
      }

      const results = data.data.results;
      
      // Ê£ÄÊµãÈóÆÈ¢òÁ±ªÂûã
      const questionType = this.detectQuestionType(results);
      
      // Ê†πÊçÆ‰∏çÂêåÈóÆÈ¢òÁ±ªÂûãÊ†ºÂºèÂåñÁ≠îÊ°à
      switch(questionType) {
        case 'disease_desc':
          return this.formatDiseaseDescription(results);
        case 'disease_acompany':
          return this.formatDiseaseComplication(results);
        case 'disease_do_food':
          return this.formatDiseaseFood(results);
        case 'disease_symptom':
          return this.formatDiseaseSymptom(results);
        case 'disease_prevent':
          return this.formatDiseasePrevent(results);
        default:
          return this.formatGeneralResults(results);
      }
    },
    
    // Ê£ÄÊµãÈóÆÈ¢òÁ±ªÂûã
    detectQuestionType(results) {
      // Ê£ÄÊü•Á¨¨‰∏Ä‰∏™ÁªìÊûúÊòØÂê¶ÊúâÂÆû‰ΩìÂíåÂ±ûÊÄß
      const firstResult = results[0];
      
      // Ê£ÄÊü•ÊòØÂê¶ÊòØÁñæÁóÖÊèèËø∞
      if (firstResult.properties && 
          firstResult.properties.desc && 
          firstResult.properties.desc.length > 50) {
        return 'disease_desc';
      }
      
      // Ê£ÄÊü•ÊòØÂê¶ÊòØÂπ∂ÂèëÁóá
      if (results.length > 3 && 
          results.every(r => r.relations && 
                            r.relations.some(rel => 
                              rel.relation && 
                              rel.relation.includes('Âπ∂ÂèëÁóá')))) {
        return 'disease_acompany';
      }
      
      // Ê£ÄÊü•ÊòØÂê¶ÊòØÈ£üÁâ©Áõ∏ÂÖ≥
      if (results.some(r => r.relations && 
                          r.relations.some(rel => 
                            rel.relation && 
                            (rel.relation.includes('ÂÆúÂêÉ') || 
                             rel.relation.includes('Êé®ËçêÈ£üË∞±'))))) {
        return 'disease_do_food';
      }
      
      // Ê£ÄÊü•ÊòØÂê¶ÊòØÁóáÁä∂Áõ∏ÂÖ≥
      if (results.some(r => r.relations && 
                          r.relations.some(rel => 
                            rel.relation && 
                            rel.relation.includes('ÁóáÁä∂')))) {
        return 'disease_symptom';
      }
      
      // Ê£ÄÊü•ÊòØÂê¶ÊòØÈ¢ÑÈò≤Áõ∏ÂÖ≥
      if (results.some(r => r.relations && 
                          r.relations.some(rel => 
                            rel.relation && 
                            rel.relation.includes('È¢ÑÈò≤')))) {
        return 'disease_prevent';
      }
      
      return 'general';
    },
    
    // Ê†ºÂºèÂåñÁñæÁóÖÊèèËø∞
    formatDiseaseDescription(results) {
      let answer = '';
      const result = results[0];
      
      if (result.entity) {
        answer += `<div class="entity-title">„Äê${result.entity}„Äë</div>`;
      }
      
      if (result.properties && result.properties.desc) {
        // Ê∏ÖÁêÜÊèèËø∞‰∏≠ÁöÑÁâπÊÆäÂ≠óÁ¨¶
        let desc = result.properties.desc.replace(/\\xa0/g, ' ').trim();
        
        // ÂàÜÊÆµËêΩÊòæÁ§∫
        const paragraphs = desc.split('\n').filter(p => p.trim());
        
        paragraphs.forEach(para => {
          answer += `<p class="desc-paragraph">${para}</p>`;
        });
      }
      
      return answer || 'ÂæàÊä±Ê≠âÔºåÊöÇÊó∂Ê≤°ÊúâÊâæÂà∞Áõ∏ÂÖ≥ÁñæÁóÖÁöÑÊèèËø∞‰ø°ÊÅØ„ÄÇ';
    },
    
    // Ê†ºÂºèÂåñÂπ∂ÂèëÁóá‰ø°ÊÅØ
    formatDiseaseComplication(results) {
      if (!results || results.length === 0) return 'Êú™ÊâæÂà∞Áõ∏ÂÖ≥Âπ∂ÂèëÁóá‰ø°ÊÅØ';
      
      const mainEntity = results[0].entity;
      let answer = `<div class="entity-title">„Äê${mainEntity}„ÄëÁöÑÂπ∂ÂèëÁóá</div>`;
      
      // ÂàõÂª∫Âπ∂ÂèëÁóáÂàÜÁ±ª
      const complications = {
        from: [], // ‰∏ªÁñæÁóÖÂºïËµ∑ÁöÑÂπ∂ÂèëÁóá
        to: []    // ÂèØËÉΩÂØºËá¥‰∏ªÁñæÁóÖÁöÑÂÖ∂‰ªñÁñæÁóÖ
      };
      
      // ÂàÜÁ±ªÂ§ÑÁêÜ
      results.forEach(result => {
        if (result.relations && result.relations.length > 0) {
          result.relations.forEach(relation => {
            if (relation.source === mainEntity) {
              // ‰∏ªÁñæÁóÖÂºïËµ∑ÁöÑÂπ∂ÂèëÁóá
              complications.from.push(relation.target);
            } else if (relation.target === mainEntity) {
              // ÂèØËÉΩÂØºËá¥‰∏ªÁñæÁóÖÁöÑÂÖ∂‰ªñÁñæÁóÖ
              complications.to.push(relation.source);
            }
          });
        }
      });
      
      // Ê†ºÂºèÂåñ‰∏ªÁñæÁóÖÂºïËµ∑ÁöÑÂπ∂ÂèëÁóá
      if (complications.from.length > 0) {
        answer += `<div class="complication-section">
          <div class="section-title">${mainEntity}ÂèØËÉΩÂºïËµ∑ÁöÑÂπ∂ÂèëÁóáÔºö</div>
          <div class="complication-list">`;
        
        complications.from.slice(0, 10).forEach(comp => {
          answer += `<div class="complication-item">‚Ä¢ ${comp}</div>`;
        });
        
        if (complications.from.length > 10) {
          answer += `<div class="complication-more">Á≠â${complications.from.length}ÁßçÂπ∂ÂèëÁóá</div>`;
        }
        
        answer += `</div></div>`;
      }
      
      // Ê†ºÂºèÂåñÂèØËÉΩÂØºËá¥‰∏ªÁñæÁóÖÁöÑÂÖ∂‰ªñÁñæÁóÖ
      if (complications.to.length > 0) {
        answer += `<div class="complication-section">
          <div class="section-title">ÂèØËÉΩÂØºËá¥${mainEntity}ÁöÑÂÖ∂‰ªñÁñæÁóÖÔºö</div>
          <div class="complication-list">`;
        
        complications.to.slice(0, 10).forEach(comp => {
          answer += `<div class="complication-item">‚Ä¢ ${comp}</div>`;
        });
        
        if (complications.to.length > 10) {
          answer += `<div class="complication-more">Á≠â${complications.to.length}ÁßçÁñæÁóÖ</div>`;
        }
        
        answer += `</div></div>`;
      }
      
      // Ê∑ªÂä†Ê≥®ÊÑè‰∫ãÈ°π
      answer += `<div class="notice-section">
        <div class="notice-title">Ê∏©È¶®ÊèêÁ§∫Ôºö</div>
        <div class="notice-content">Âπ∂ÂèëÁóá‰ø°ÊÅØ‰ªÖ‰æõÂèÇËÄÉÔºåÂÖ∑‰ΩìËØäÊñ≠ÂíåÊ≤ªÁñóËØ∑Âí®ËØ¢‰∏ì‰∏öÂåªÁîü„ÄÇ</div>
      </div>`;
      
      return answer;
    },
    
    // Ê†ºÂºèÂåñÈ£üÁâ©‰ø°ÊÅØ
    formatDiseaseFood(results) {
      if (!results || results.length === 0) return 'Êú™ÊâæÂà∞Áõ∏ÂÖ≥È•ÆÈ£ü‰ø°ÊÅØ';
      
      const mainEntity = results[0].entity;
      let answer = `<div class="entity-title">„Äê${mainEntity}„ÄëÁöÑÈ•ÆÈ£üÂª∫ËÆÆ</div>`;
      
      // ÂàÜÁ±ªÈ£üÁâ©
      const foods = {
        recommended: [], // ÂÆúÂêÉÈ£üÁâ©
        recipes: []      // Êé®ËçêÈ£üË∞±
      };
      
      // ÂàÜÁ±ªÂ§ÑÁêÜ
      results.forEach(result => {
        if (result.relations && result.relations.length > 0) {
          result.relations.forEach(relation => {
            if (relation.relation && relation.relation.includes('ÂÆúÂêÉ')) {
              foods.recommended.push(relation.target);
            } else if (relation.relation && relation.relation.includes('Êé®ËçêÈ£üË∞±')) {
              foods.recipes.push(relation.target);
            }
          });
        }
      });
      
      // Ê†ºÂºèÂåñÂÆúÂêÉÈ£üÁâ©
      if (foods.recommended.length > 0) {
        answer += `<div class="food-section">
          <div class="section-title">ÂÆúÂêÉÈ£üÁâ©Ôºö</div>
          <div class="food-grid">`;
        
        foods.recommended.forEach(food => {
          answer += `<div class="food-item">
            <span class="food-icon">üçΩÔ∏è</span>
            <span class="food-name">${food}</span>
          </div>`;
        });
        
        answer += `</div></div>`;
      }
      
      // Ê†ºÂºèÂåñÊé®ËçêÈ£üË∞±
      if (foods.recipes.length > 0) {
        answer += `<div class="food-section">
          <div class="section-title">Êé®ËçêÈ£üË∞±Ôºö</div>
          <div class="recipe-list">`;
        
        foods.recipes.forEach(recipe => {
          answer += `<div class="recipe-item">
            <span class="recipe-icon">üç≤</span>
            <span class="recipe-name">${recipe}</span>
          </div>`;
        });
        
        answer += `</div></div>`;
      }
      
      // Ê∑ªÂä†È•ÆÈ£üÂª∫ËÆÆ
      answer += `<div class="notice-section">
        <div class="notice-title">È•ÆÈ£üÊ≥®ÊÑè‰∫ãÈ°πÔºö</div>
        <div class="notice-content">ÂêàÁêÜÈ•ÆÈ£üÊòØÊéßÂà∂ÁñæÁóÖÁöÑÈáçË¶ÅÊâãÊÆµÔºåÂª∫ËÆÆÈÖçÂêàÂåªÁîüÊåáÂØºÂà∂ÂÆö‰∏™ÊÄßÂåñÈ•ÆÈ£üÊñπÊ°à„ÄÇ</div>
      </div>`;
      
      return answer;
    },
    
    // Ê†ºÂºèÂåñÁóáÁä∂‰ø°ÊÅØ
    formatDiseaseSymptom(results) {
      if (!results || results.length === 0) return 'Êú™ÊâæÂà∞Áõ∏ÂÖ≥ÁóáÁä∂‰ø°ÊÅØ';
      
      const mainEntity = results[0].entity;
      let answer = `<div class="entity-title">„Äê${mainEntity}„ÄëÁöÑÁóáÁä∂</div>`;
      
      // Êî∂ÈõÜÁóáÁä∂
      const symptoms = [];
      
      results.forEach(result => {
        if (result.relations && result.relations.length > 0) {
          result.relations.forEach(relation => {
            if (relation.relation && relation.relation.includes('ÁóáÁä∂')) {
              symptoms.push(relation.target);
            }
          });
        }
      });
      
      // Ê†ºÂºèÂåñÁóáÁä∂ÂàóË°®
      if (symptoms.length > 0) {
        answer += `<div class="symptom-section">
          <div class="symptom-list">`;
        
        symptoms.forEach(symptom => {
          answer += `<div class="symptom-item">‚Ä¢ ${symptom}</div>`;
        });
        
        answer += `</div></div>`;
      } else {
        answer += '<p>Êú™ÊâæÂà∞Áõ∏ÂÖ≥ÁóáÁä∂‰ø°ÊÅØ</p>';
      }
      
      return answer;
    },
    
    // Ê†ºÂºèÂåñÈ¢ÑÈò≤‰ø°ÊÅØ
    formatDiseasePrevent(results) {
      if (!results || results.length === 0) return 'Êú™ÊâæÂà∞Áõ∏ÂÖ≥È¢ÑÈò≤‰ø°ÊÅØ';
      
      const mainEntity = results[0].entity;
      let answer = `<div class="entity-title">„Äê${mainEntity}„ÄëÁöÑÈ¢ÑÈò≤Êé™ÊñΩ</div>`;
      
      // Êî∂ÈõÜÈ¢ÑÈò≤‰ø°ÊÅØ
      const preventions = [];
      
      results.forEach(result => {
        if (result.relations && result.relations.length > 0) {
          result.relations.forEach(relation => {
            if (relation.relation && relation.relation.includes('È¢ÑÈò≤')) {
              preventions.push(relation.target);
            }
          });
        }
      });
      
      // Ê†ºÂºèÂåñÈ¢ÑÈò≤ÂàóË°®
      if (preventions.length > 0) {
        answer += `<div class="prevention-section">
          <div class="prevention-list">`;
        
        preventions.forEach((prevention, index) => {
          answer += `<div class="prevention-item">
            <div class="prevention-number">${index + 1}</div>
            <div class="prevention-content">${prevention}</div>
          </div>`;
        });
        
        answer += `</div></div>`;
      } else {
        answer += '<p>Êú™ÊâæÂà∞Áõ∏ÂÖ≥È¢ÑÈò≤‰ø°ÊÅØ</p>';
      }
      
      return answer;
    },
    
    // Ê†ºÂºèÂåñÈÄöÁî®ÁªìÊûú
    formatGeneralResults(results) {
      let formattedAnswer = '';

      results.forEach((result, index) => {
        if (index > 0) formattedAnswer += '\n\n';
        
        // Ê∑ªÂä†ÂÆû‰Ωì‰ø°ÊÅØ
        if (result.entity) {
          formattedAnswer += `<div class="entity-title">„Äê${result.entity}„Äë</div>`;
        }
        
        // Ê∑ªÂä†Â±ûÊÄß‰ø°ÊÅØ
        if (result.properties && Object.keys(result.properties).length > 0) {
          formattedAnswer += '<div class="properties-section">';
          formattedAnswer += '<div class="section-title">Â±ûÊÄß‰ø°ÊÅØÔºö</div>';
          
          Object.entries(result.properties).forEach(([key, value]) => {
            // Ë∑≥ËøáÁ©∫ÂÄºÊàñnameÂ±ûÊÄß(Â∑≤ÁªèÂú®ÂÆû‰ΩìÊ†áÈ¢ò‰∏≠ÊòæÁ§∫)
            if (!value || key === 'name') return;
            
            // ÂØπ‰∫éËæÉÈïøÁöÑÊèèËø∞‰ø°ÊÅØ
            if (key === 'desc' && typeof value === 'string' && value.length > 100) {
              const paragraphs = value.replace(/\\xa0/g, ' ').split('\n').filter(p => p.trim());
              formattedAnswer += `<div class="property-item"><span class="property-key">${key}:</span>`;
              paragraphs.forEach(para => {
                formattedAnswer += `<p class="property-value-paragraph">${para}</p>`;
              });
              formattedAnswer += '</div>';
            } else {
              formattedAnswer += `<div class="property-item">
                <span class="property-key">${key}:</span>
                <span class="property-value">${value}</span>
              </div>`;
            }
          });
          
          formattedAnswer += '</div>';
        }
        
        // Ê∑ªÂä†ÂÖ≥Á≥ª‰ø°ÊÅØ
        if (result.relations && result.relations.length > 0) {
          formattedAnswer += '<div class="relations-section">';
          formattedAnswer += '<div class="section-title">Áõ∏ÂÖ≥‰ø°ÊÅØÔºö</div>';
          
          const relationsByType = {};
          
          // ÂØπÂÖ≥Á≥ªËøõË°åÂàÜÁªÑ
          result.relations.forEach(relation => {
            const type = relation.relation || 'ÂÖ∂‰ªñ';
            if (!relationsByType[type]) {
              relationsByType[type] = [];
            }
            relationsByType[type].push(relation);
          });
          
          // ÊåâÂÖ≥Á≥ªÁ±ªÂûãËæìÂá∫
          Object.entries(relationsByType).forEach(([type, relations]) => {
            formattedAnswer += `<div class="relation-group">
              <div class="relation-type">${type}Ôºö</div>
              <div class="relation-items">`;
              
            relations.forEach(relation => {
              formattedAnswer += `<div class="relation-item">‚Ä¢ ${relation.target || relation.source}</div>`;
            });
            
            formattedAnswer += '</div></div>';
          });
          
          formattedAnswer += '</div>';
        }
      });

      return formattedAnswer || 'ÂæàÊä±Ê≠âÔºåÊöÇÊó∂Ê≤°ÊúâÊâæÂà∞Áõ∏ÂÖ≥‰ø°ÊÅØ„ÄÇËØ∑Â∞ùËØïÊç¢‰∏ÄÁßçÊñπÂºèÊèêÈóÆÔºåÊàñËÄÖÂí®ËØ¢ÂÖ∂‰ªñÂåªÁñóÈóÆÈ¢ò„ÄÇ';
    },

    async sendMessage() {
      if (!this.inputText) return;

      try {
        // Ê∑ªÂä†Áî®Êà∑Ê∂àÊÅØ
        this.messages.push({
          id: this.messageId++,
          text: this.inputText,
          isUser: true,
          timestamp: Date.now()
        });

        // ‰øùÂ≠òÂΩìÂâçÈóÆÈ¢ò
        const currentQuestion = this.inputText;
        this.inputText = '';
        
        // ÁÑ¶ÁÇπÂõûÂà∞ËæìÂÖ•Ê°Ü
        this.$nextTick(() => {
          this.$refs.inputField.focus();
        });

        this.isLoading = true;
        
        // Ëá™Âä®ÊªöÂä®Âà∞Â∫ïÈÉ®
        this.scrollToBottom();
        
        const response = await axios.post('http://127.0.0.1:8000/api/qa/', { 
          question: currentQuestion 
        });

        // Â§ÑÁêÜÂìçÂ∫î
        const answer = response.data?.success
          ? this.findAnswerInData(response.data)
          : 'Êä±Ê≠âÔºåÊúçÂä°Âô®Â§ÑÁêÜÊÇ®ÁöÑËØ∑Ê±ÇÊó∂ÈÅáÂà∞‰∫ÜÈóÆÈ¢òÔºåËØ∑Á®çÂêéÂÜçËØï„ÄÇ';

        // Ê∑ªÂä†Á≥ªÁªüÊ∂àÊÅØ
        this.messages.push({
          id: this.messageId++,
          text: answer,
          isUser: false,
          timestamp: Date.now()
        });

      } catch (error) {
        console.error(`ËØ∑Ê±ÇÂ§±Ë¥•Ôºö${error.response?.data?.message || error.message}`);
        // Ê∑ªÂä†ÈîôËØØÊ∂àÊÅØ
        this.messages.push({
          id: this.messageId++,
          text: 'Êä±Ê≠âÔºåËøûÊé•ÊúçÂä°Âô®Êó∂ÈÅáÂà∞‰∫ÜÈóÆÈ¢ò„ÄÇËØ∑Ê£ÄÊü•ÊÇ®ÁöÑÁΩëÁªúËøûÊé•ÔºåÊàñÁ®çÂêéÂÜçËØï„ÄÇ',
          isUser: false,
          timestamp: Date.now()
        });
      } finally {
        this.isLoading = false;
        // Ëá™Âä®ÊªöÂä®Âà∞Â∫ïÈÉ®
        this.scrollToBottom();
      }
    },
    
    // ÊªöÂä®Âà∞Â∫ïÈÉ®
    scrollToBottom() {
      this.$nextTick(() => {
        const container = this.$refs.messageList;
        container.scrollTop = container.scrollHeight;
      });
    }
  },
};
</script>

